import secFooter from "@/components/SecFooter/SecFooter.vue"
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default {
	data() {
		return {
			tabarA: 2,
			dataList: [],
			page: 1,
			totalPage: 1,
			status: 'loading'
		}
	},
	components: {
		secFooter,
		uniLoadMore
	},
	onReachBottom() {
		this.status = "loading";
		this.totalPage > this.page ? (this.page += 1, this.loadData()) : setTimeout(() => {
			this.status = "noMore"
		}, 30)
	},
	onLoad() {
		this.loadData()
	},
	methods: {
		loadData() {
			this._reqw.ipost({
				nowPage: this.page
			}, "newsList").then(res => {
				res.totalPage == 1 ? this.status = "noMore" : this.status = "more";
				res.result == 0 ? (this.totalPage = res.totalPage, res.dataList
					.forEach(item => {
						this.dataList.push(item)
					})) : this.$api.tip(
					res.resultNote);
			}).catch(err => {})
		},
		details(type, e) {
			if (e.video == '') {
				this.$api.navTo(`/pagesD/zixun/zixunDetail?id=${e.id}&name=${e.title}`)
			} else {
				if (type == 1) {
					this.$api.navTo(`/pagesD/zixun/zixunDetail?id=${e.id}&name=${e.title}`)
				}
			}


		}
	}
}
